body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.outer-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  justify-items:center;
  min-height:100vh;
  margin-top:-10em;

}

.outer-box{
    width:500px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    border:1px solid #bfbfbf;
    margin:auto;
}

.header{
    height:100px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width:100%;
    background: #d9d9d9;
    font-size:24px;
    font-weight: 400;
}

.inner-box{
    width:400px;
    display: -webkit-flex;
    display: flex;
    padding:30px; 
    margin:50px; 
    -webkit-justify-content: center; 
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; 
    text-align: center;
    background: white;
    font-size: 16px;
}

#userinput{
    width:300px;
    font-size: 16px;
}

#password{
    width:300px;
    font-size: 16px;
}

#submit-button{
    background: DodgerBlue;
    border:none;
    color:white;
    width:100px;
    font-size: 16px;
    margin-top:10px;
    padding:10px;
}


.order-wrapper{
    width:80%;
    margin:10px auto 5px auto;
    font-size: 16px;
}

.orderlist-wrapper{
  width:80%;
  margin:40px auto 5px auto;
  font-size: 16px;
}
/*background: #e6e6e6;*/

.orderdetail-wrapper{
    width:80%;
    margin:20px auto auto auto;
    font-size: 16px;
    height:650px; 
    padding:10px;
}

.orderdetail-header{
    width:100%;
    margin:5px auto 5px auto;
    height:50px;
    background: transparent;
    border:none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: left;
            justify-content: left;
    padding:0px 5px 5px 0px;
}

.input_spinner{
    width:120px;
    text-align: center;
}

.remove_btn {
    background-color: transparent; 
    border: #a6a6a6 1px solid; 
    color: DodgerBlue; 
    margin-left: 25px; 
    font-size: 20px; 
    cursor: pointer; 
  }

  .add_cart_btn{
    background-color: DodgerBlue; 
    border:none; 
    color:white; 
    font-size: 16px; 
    cursor: pointer; 
  }

  .add_cart_btn:hover{
    background-color: green;  
  }
  
 
  .remove_btn:hover {
    color: green;
  }

  .table_cell{
    padding:7px !important;
    vertical-align:center;
    text-align: center;
  }

  .table_row{
    padding:0px;
    margin:0px;
}



  #page-numbers {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: right;
            justify-content: right;
    width:100%;
    margin:10px auto auto auto;
  }
  
  #page-numbers > li {
    margin-right: 0.3em;
    color: white;
    background: DodgerBlue;
    border: #a6a6a6 1px solid;
    padding:3px 7px 3px 7px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    font-size:14px;
  }

  #tab_menu{
    font-size:16px;
  }

  .orderdetail-footer{
    width:80%;
    margin:20px auto 0px auto;
    height:50px;
    background: transparent;
    border:none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: right;
            justify-content: right;
    padding:5px 5px 5px 0px;
}

.update_cart_btn{
  background-color: green; 
  border:none; 
  color:white; 
  font-size: 16px; 
  cursor: pointer; 
}

#table-cart td,
#table-cart th
{
  border:1px solid #8c8c8c;
}

#table-order td,
#table-order th
{
  border:none;
  outline-color:none;
  padding: 0px;
  margin:0px;
}

.th-label{
  background:#d9d9d9;
  padding : 10px 20px 10px 20px;
}

.order-filter{
  width:200px;
  height:50px;
  margin:0px auto 20px auto;
  padding:5px;
}




  

  

  
  #container {
    padding-top:20px;
    max-height: 700px;
    width: 100%;
    display: block;
    position: relative;
    overflow: hidden;
}

#tabs {
    height: 50px;
    width: 100%;
    background-color: #f1f1f1;
}

#tabButton {
  float: left;
  background: #f1f1f1;
  cursor: pointer;
  outline:none;
  border:none;
  height:50px;
  font-size:16px;
  padding: 15px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

#content {
    height: 630px;
    width: 100%;
    border: 2px solid rgb(212, 208, 208);
    overflow-y: auto;
    font-size:16px;
}
  
  .tab button:hover {
    background-color: #ddd;
  }
  
  .tab button.active {
    background-color: #ccc;
  }
  
  .detail-panel{
    height:130px;
    width:80%;
    margin:10px;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left:50px;  
  }

  .card-wrapper{
    width:100%;;
    height:150px;
    border:1px solid #e6e6e6;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:row;
            flex-direction:row;
  }

  .card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width:200px;
    height:100px;
    margin:20px;
    border-radius:25%;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
  
  .card-container {
    height:100%;
    width:100%;
    padding: 0px;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
   border:1px slod grey;
  }

  .h-row{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  #data-input{
    height:30px;
    margin-top:0px;
    text-align:center;
    width:100px;
  }

  #close-btn{
    font-size:16px;
    padding:5px;
    color:black;
    background: rgb(196, 196, 196);
  }

.navbar {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    overflow: hidden;
    background-color: black;
    position: fixed; 
    top: 0; 
    width: 100%; 
    height:70px;
    
  }

  .navbar p{
    margin: auto auto auto 20px;
    color:white;
    font-size: 20pt;
  }

  .content{
    overflow-y: auto;
  }

  .user-panel{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width:200px;
  }

  .user_icon_div{
    border-radius:50%;
    display:-webkit-flex;
    display:flex;
    justify-items: center;
    height:50px;
    width:50px;
    background: rgb(224, 219, 219);
    margin:auto 10px auto auto;
  }

  #user_icon{
    font-size: 26px;
    margin:auto;
  }

  .user_Label{
    margin:auto 20px auto 10px;
    color:white;
    font-size :20px;
    font-weight:0.8;
  }
